*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body{
  width: 100%;
  height: 100%;
}

header, footer{
  text-align: center;
  padding: 1%;
}

//stylowanie mapki

img{
  width: 100%;
  height: auto;
}

@mixin width80p{
  width: 80%;
  margin: 0 auto;
}

@mixin auto-height{
  height: 700%;
  overflow: auto;
  @include width80p;
}


@media screen and (max-width: 599px) {
  #mainContainer{
    width: 100%;
    height: 85%;
    overflow: auto;
  }

  #menu, #menu ul, #menu ol {
    list-style-type:none;
    padding:0;
    margin:0;
  }

  #menu {
    background-color:#FFF;
    font-size:18px;
    height:2em;
    width:100%;
    line-height:2em;
    text-align:center;
  }

  #menu li {
    width:100%;
    display:block;
    text-decoration:none;
    color:#000;
    padding:0 5px;
  }

  #menu > li {    
    @include width80p;
    background-color:#FDD700;
    height:2em;
  }

  #menu > li:hover { background-color:#EEE; }
  #menu > li:hover > li { color:#09C; }
  #menu > li:hover > ul { display:block; }

  #menu > li > ul { display:none; @include width80p; }    
  #menu > li:first-child > ul{ @include auto-height; }
  #menu > li:nth-child(3) > ul{ @include auto-height; }
  #menu > li:nth-child(4) > ul{ @include auto-height; }
      
   #menu > li > ul > li {
    position:relative;
    background-color:#EEE;
    border-top:1px solid #FFF;
  }

  #menu > li > ul > li:hover {
    background-color:#DDD;
    color:#09C;
  }

 #menu > li > ul > li > ol {
    display:none;
    width:80%;
    height: 300%;
    overflow: auto;
    position:absolute;   
    margin-top:-4em;
  }

  #menu > li > ul > li:hover > ol { display:block; }

  #menu > li > ul > li > ol > li {
    background-color:#CCC;
    border-top:1px solid #FFF;
    border-left:1px solid #FFF;
  }

  #menu > li > ul > li > ol > li:hover { color:#09C; }
  #menu li, ol li { border-radius:6px;  }
}
















